<template>
  <section id="availableTable">

    <div class="tableCont">
      <div class="blo">
        <p class="blo_name">姓名</p>
        <input type="text" maxlength="20" v-model="nameInfo.text" class="ipt" placeholder="请填写您的姓名" :class="{'err':nameInfo.isHint}">
        <div class="hint" v-if="nameInfo.isHint">{{nameInfo.hintText}}</div>
      </div>
      <div class="blo">
        <p class="blo_name">公司名称</p>
        <input type="text" maxlength="50" v-model="companyNameInfo.text" class="ipt" placeholder="请填写您的公司名称" :class="{'err':companyNameInfo.isHint}">
        <div class="hint" v-if="companyNameInfo.isHint">{{companyNameInfo.hintText}}</div>
      </div>
      <div class="blo">
        <p class="blo_name">职位</p>
        <input type="text" maxlength="20" v-model="positionNameInfo.text" class="ipt" placeholder="请填写您的职位" :class="{'err':positionNameInfo.isHint}">
        <div class="hint" v-if="positionNameInfo.isHint">{{positionNameInfo.hintText}}</div>
      </div>
      <div class="blo">
        <p class="blo_name">手机</p>
        <input type="text" maxlength="11" v-model="mobileInfo.text" class="ipt" placeholder="请填写您的手机号" :class="{'err':mobileInfo.isHint}">
        <div class="hint" v-if="mobileInfo.isHint">{{mobileInfo.hintText}}</div>
      </div>
    </div>
    <div class="annotation">
      <h3 class="text1">注：</h3>
      <p class="text2">填写报名信息后，请准备好企业简介及企业logo，我们将会有专门的【商务经理】和您联系！</p>
      <p class="text2">咨询请致电：400-065-5788</p>
    </div>

    <div class="btn" @click="submit">提交</div>
  </section>
</template>
<style lang="less">
#availableTable {
  width: 320px;
  margin: 0 auto;
  padding-bottom: 30px;
  .head {
    .head_tit {
      font-size: 18px;
      font-weight: normal;
      color: rgb(34, 34, 34);
      padding: 25px 0px 10px 0;
      text-align: left
    }
    .logo {
      width: 320px;
      height: 181px;
      display: block;
    }
  }
  .tableCont {
    padding-top: 20px;
    .blo {
      padding-top: 15px;
      padding-bottom: 15px;
      .blo_name {
        font-size: 14px;
        font-weight: bold;
        color: rgb(34, 34, 34);
        margin-bottom: 4px;
        &:after {
          content: '*';
          color: #FF3824;
          margin-top: 1px;
          display: inline-block;
          line-height: 0;
          vertical-align: middle;
        }
      }
      .ipt {
        width: 320px;
        height: 40px;
        line-height: 40px;
        box-sizing: border-box;
        padding: 0 5px;
        border: 1px solid #ccc;
        font-size: 14px;

        &.err {
          border: 1px solid red;
        }
      }
      .hint {
        color: #FF2851;
        text-indent: 5px;
        margin-top: 5px;
      }
    }
  }
  .annotation {
    padding-top: 15px;
    padding-bottom: 15px;
    .text1 {
      font-size: 16px;
      line-height: 1.42857;
      color: #333;
    }
    .text2 {
      font-size: 14px;
      line-height: 1.4;
      color: #545454;
    }
  }
  .btn {
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-color: rgb(40, 117, 232);
    font-size: 14px;
    color: rgb(255, 255, 255);
    border-width: 0;
    border-color: rgb(255, 255, 255);
    text-align: center;
  }
}
  
</style>
<script>
import Vue from 'vue'
import Component from 'vue-class-component'
import axios from 'axios'
import wechatMixin from '@/mixins/wechat'
import {addApplyApi} from '@/store/api/available'
import localstorage from '@/utils/localstorage'
import { Toast } from 'mint-ui';

Vue.component(Toast.name, Toast);
const wx = Vue.wx
@Component({
	components: {
  },
  mixins: [wechatMixin]
})
export default class PageIndex extends Vue {
  mobileReg = /^1(3|4|5|6|7|8|9)\d{9}$/
  userNameReg = /^.{2,20}$/
  positionReg = /^.{2,20}$/
  companyNameReg = /^[\u0391-\uFFE5A-Za-z]{2,50}$/

  nameInfo = {
    text:'',
    isHint: false,
    hintText: ''
  }

  mobileInfo = {
    text:'',
    isHint: false,
    hintText: ''
  }

  positionNameInfo = {
    text:'',
    isHint: false,
    hintText: ''
  }

  companyNameInfo = {
    text:'',
    isHint: false,
    hintText: ''
  }

  //侧边栏展示
  navSideStyle = 'close'

  examine() {
    let name = this.nameInfo.text
    let mobile = this.mobileInfo.text
    let positionName = this.positionNameInfo.text
    let companyName = this.companyNameInfo.text

    let err = 0
    if (!name) {
      this.nameInfo.isHint = true
      this.nameInfo.hintText = '请填写姓名'
      err+=1
    }else if (name && !this.userNameReg.test(name)) {
      this.nameInfo.isHint = true
      this.nameInfo.hintText = '姓名需为2-20个汉字或英文'
      err+=1
    }else {
      this.nameInfo.isHint = false
      this.nameInfo.hintText = ''
    }

    if (!mobile) {
      this.mobileInfo.isHint = true
      this.mobileInfo.hintText = '请填写手机号'
      err+=1
    }else if (name && !this.mobileReg.test(mobile)) {
      this.mobileInfo.isHint = true
      this.mobileInfo.hintText = '手机号格式不正确'
      err+=1
    }else {
      this.mobileInfo.isHint = false
      this.mobileInfo.hintText = ''
    }

    if (!positionName) {
      this.positionNameInfo.isHint = true
      this.positionNameInfo.hintText = '请填写职位'
      err+=1
    }else if (positionName && !this.positionReg.test(
      positionName)) {
      this.positionNameInfo.isHint = true
      this.positionNameInfo.hintText = '职位名称应在2-20字符之内'
      err+=1
    }else {
      this.positionNameInfo.isHint = false
      this.positionNameInfo.hintText = ''
    }

    if (!companyName) {
      this.companyNameInfo.isHint = true
      this.companyNameInfo.hintText = '请填写公司名称'
      err+=1
    }else if (companyName && !this.companyNameReg.test(companyName)) {
      this.companyNameInfo.isHint = true
      this.companyNameInfo.hintText = '公司名称应在2-50字符之内'
      err+=1
    }else {
      this.companyNameInfo.isHint = false
      this.companyNameInfo.hintText = ''
    }

    if(err>0){
      return false
    }else {
      return true
    }
  }

  submit() {
    if(this.examine()){
      this.addApply()
    }
  }  

  addApply(){
    addApplyApi({
      company_name: this.companyNameInfo.text,
      name: this.nameInfo.text,
      position: this.positionNameInfo.text,
      mobile: this.mobileInfo.text
    }).then(res => {
      

      Toast({
        message: '提交成功',
        iconClass: 'icon icon-success',
        duration: 2000
      });

      setTimeout(()=>{
        this.$router.go(-1)
      },2000)
    }).catch(err => {
      Toast({
        message: '提交失败',
        iconClass: 'icon icon-success',
        duration: 2000
      });
    })
  }

  mounted () {
  }

  //判断是否是微信浏览器的函数
  get isWeiXin() {
    var ua = window.navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == 'micromessenger'
  }
}
</script>
